import { Modal, Form, Card, Input, Radio, Select } from 'antd';
import * as React from 'react';
import { FormComponentProps } from 'antd/lib/form';
import { IBoardAddParam } from '@/api';
import { toastformError } from '@/utils';
import { IAction } from '@/types';
import { formItemLayout } from '@/constants';

interface Props extends FormComponentProps {
  visible: boolean;
  onClose: (param: boolean) => any;
  projectId: number;
  onSubmit: (param: IBoardAddParam) => IAction;
}

const BoardAddModel = (props: Props) => {
  const { getFieldDecorator } = props.form;
  React.useEffect(() => {
    props.form.resetFields();
  }, [props.visible]);
  const handleSubmit = (e: React.MouseEvent) => {
    e.preventDefault();
    props.form.validateFields((err, values) => {
      if (err) {
        toastformError(err);
        return;
      }
      props.onSubmit({ projectId: props.projectId, ...values, layout: [] });
      props.onClose(false);
    });
  };

  return (
    <Modal onOk={handleSubmit} title='' visible={props.visible} onCancel={() => props.onClose(false)}>
      <Form layout='horizontal' {...formItemLayout}>
        <Form.Item label=''>
          {getFieldDecorator('name', {
            rules: [{ required: true, message: '' }]
          })(<Input placeholder='' />)}
        </Form.Item>

        {/* <Form.Item label=''>
          {getFieldDecorator('public', {
            initialValue: true
          })(
            <Radio.Group>
              <Radio value={true}></Radio>
              <Radio value={false}></Radio>
            </Radio.Group>
          )}
        </Form.Item> */}
        <Form.Item label=''>
          {getFieldDecorator('description', {})(<Input placeholder='' />)}
        </Form.Item>
      </Form>
    </Modal>
  );
};

export default Form.create<Props>()(BoardAddModel);
